import { useState } from "react";
import { atom, useRecoilState } from 'recoil'
import { recoilPersist } from 'recoil-persist'

const { persistAtom } = recoilPersist()

export type Schedule = {
  time: string;
  value: string;
  image: string;
}
const scheduleState = atom({
  key: 'schedule',
  default: [],
  effects_UNSTABLE: [persistAtom],
})

export function useGenerateschedule() {
  const [schedule, setSchedule] = useRecoilState<Schedule[]>(scheduleState);
  const API_URL = process.env.REACT_APP_API_URL;

  const fetchSchedule = async (_prompt: string) => {
    const prompt = `${_prompt}24512json[{"time": "00:00", "value": ""}, {"time": "01:00", "value": ""}]`
    const url = `${API_URL}/chat?prompt=${encodeURIComponent(prompt)}`;

    const res = await fetch(url)
    if (res) {
      const _schedule = await res.json();
      setSchedule(_schedule);
    }
  }


  return {
    schedule,
    fetchSchedule,
    setSchedule
  }
}
